import { motion } from 'framer-motion';
import { Upload } from 'lucide-react';
import { Button } from '@/components/ui/button';

export const PDFUploader = ({ onUpload }: { onUpload: (file: File) => void }) => {
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      // Validate file type
      if (file.type === 'application/pdf' || file.name.toLowerCase().endsWith('.pdf')) {
        onUpload(file);
      } else {
        alert('Please select a valid PDF file.');
      }
    }
  };

  return (
    <motion.div 
      className="text-center p-8"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <motion.div
        whileHover={{ scale: 1.1 }}
        transition={{ type: "spring", damping: 20, stiffness: 300 }}
      >
        <Upload className="w-16 h-16 text-blue-500 dark:text-blue-400 mx-auto mb-6" />
      </motion.div>
      
      <motion.div
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
        transition={{ type: "spring", damping: 20, stiffness: 300 }}
      >
        <Button 
          asChild
          className="bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white px-8 py-4 rounded-xl font-medium shadow-lg hover:shadow-xl transition-all duration-300 backdrop-blur-sm border border-white/10"
        >
          <label htmlFor="file-upload" className="cursor-pointer flex items-center gap-2">
            <Upload className="w-5 h-5" />
            Upload PDF File
          </label>
        </Button>
      </motion.div>
      
      <p className="text-sm text-slate-500 dark:text-slate-400 mt-4">
        Select a PDF file with form fields to get started
      </p>
      
      <input
        id="file-upload"
        type="file"
        accept=".pdf,application/pdf"
        onChange={handleFileChange}
        className="hidden"
      />
    </motion.div>
  );
};
